/* @import url('https://fonts.googleapis.com/css2?family=Lato&display=swap'); */

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}
.navbar {
  width: 105%;
  max-width: 1920px;
  min-width: 480px;
  margin-left: -8px;
  overflow: hidden;
  background: #000000;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: sticky;
  top: 0;
  z-index: 999;
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  height: 80px;
}

.container {
  z-index: 1;
  width: 105%;
  max-width: 1920px;
  min-width: 480px;
  margin-left: -8px;
  overflow: hidden;
  margin-right: auto;
  margin-left: auto;
  padding-right: 50px;
  padding-left: 50px;
}

.navbar-logo {
  color: #ffffff;
  justify-self: start;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
}

.navbar-icon {
  margin-right: 0.5rem;
}

.nav-menu {
  display: flex;
  align-items: center;
  justify-content: center;
  list-style: none;
  text-align: center;
  justify-content: end;
}

.nav-item {
  height: 80px;
  border-bottom: 2px solid transparent;
}

.nav-item:hover {
  border-bottom: 2px solid #fbff00;
}

.nav-links {
  color: #ffffff;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
}

.home-banner {
  height: 100vh;
  width: 105%;
  max-width: 1920px;
  min-width: 480px;
  margin-left: -8px;
  overflow: hidden;
  background-color: #000000;
}

.home-banner-text-wrapper {
  position: absolute;
  top: 50%;
  display: block;
  width: 100%;
  transform: translateY(-50%);
  text-align: center;
  color: #ffffff;
}

.home-banner-title {
  margin: 0 15px;
  font-size: 50px;
  opacity: 0;
  transition: all 1s ease;
}

.home-banner-subtitle {
  margin: 15px 15px;
  font-size: 25px;
  opacity: 0;
  transition: all 1s ease;
}

.home-banner-text-wrapper .home-banner-title {
  opacity: 1;
  font-size: 55px;
  letter-spacing: 20px;
  font-weight: 300;
}

.home-banner-text-wrapper .home-banner-subtitle {
  opacity: 1;
  font-size: 18px;
  letter-spacing: 5px;
  font-weight: 100;
}

@media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    background: #000000;
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
  }

  .nav-links:hover {
    color: #fbff00;
    transform: scale(1.2);
    transition: all 0.3s ease;
  }

  .nav-item:hover {
    border: none;
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(25%, 50%);
  }

  .menu-icon {
    color: #ffffff;
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .nav-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 120px;
  }
}
